import * as Yup from "yup";

export const registrationValidationSchema = Yup.object().shape({
	name: Yup.string().required(" "),
	surname: Yup.string().required(" "),
	email: Yup.string()
		.required(" ")
		.email("  "),
	password: Yup.string()
		.required(" ")
		.min(5, "    5 "),

	city: Yup.string().required(" "),
	studyPlace: Yup.string().required("  "),
});
